<script>
	export default {
		onLaunch: function() {
			console.log('App Launch');
			const token = uni.getStorageSync('TOKEN');
			if(token){
				uni.switchTab({
					url: '/pages/dashboard/dashboard',
					success() {
						uni.hideLoading();
					}
				});
			}else{
				uni.reLaunch({
				    url: '/pages/login/login'
				});
				// uni.showToast({
				// 	title:'登录过期！',
				// 	icon:'none',
				// 	success:function(){
						// uni.navigateTo({
						//     url: '/pages/login/login'
						// });
				// 	}
				// })
			}
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

